.choice__items {
  margin-top: 90px;
  padding: 0 190px;
  @media (max-width: 1920px) {
    margin-top: 4.69vw;
    padding: 0;
  }
}
.choice__row {
  @media (min-width: 1920px) {
    margin: -40px -30px;
  }
  @media (min-width: 801px) {
    margin: -2.08vw -1.56vw;
    display: flex;
    flex-wrap: wrap;
  }
  /*  display: flex;
  flex-wrap: wrap; */
}
.choice__item {
  @media (max-width: 1920px) {
    padding: 40px 30px;
  }
  @media (min-width: 801px) {
    padding: 2.08vw 1.56vw;
    flex: 0 1 33.333%;
  }
}
