.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  .main {
    flex: 1 1 auto;
  }
}
.container {
  margin: 0 auto;
  max-width: 100%;
}

h1 {
  font-family: Cocogoose Narrow;
  font-size: 78px;
  font-weight: 900;
  line-height: 96px;
  letter-spacing: 0%;
  text-align: center;
  text-transform: uppercase;
  @media (max-width: 1920px) {
    font-size: 4.06vw;
    line-height: 5vw;
  }
  @media (max-width: 800px) {
    font-size: 64px;
    line-height: 68px;
  }
  span {
    font-size: 62px;
    font-weight: 400;
    line-height: 90px;
    display: block;
    @media (max-width: 1920px) {
      font-size: 3.23vw;
      line-height: 4.69vw;
    }
    @media (max-width: 800px) {
      font-size: 48px;
      line-height: 57px;
    }
  }
}
h2 {
  font-family: Cocogoose Narrow;
  font-size: 64px;
  font-weight: 900;
  line-height: 79px;
  letter-spacing: 0%;
  text-align: center;
  text-transform: uppercase;
  @media (max-width: 1920px) {
    font-size: 3.33vw;
    line-height: 4.11vw;
  }
  @media (max-width: 800px) {
    font-size: 8vw;
    line-height: 9.88vw;
  }
}
h3 {
  color: rgb(17, 132, 0);
  font-family: Cocogoose Narrow;
  font-size: 48px;
  font-weight: 600;
  line-height: 59px;
  letter-spacing: 0%;
  text-align: center;
  text-transform: uppercase;
  @media (max-width: 1920px) {
    font-size: 2.5vw;
    line-height: 3.07vw;
  }
  @media (max-width: 800px) {
    font-size: 6vw;
    line-height: 7.38vw;
  }
}
.red-color {
  color: #ff002b;
}
.green-color {
  color: #118400;
}
.lines {
  overflow: hidden;
  span {
    position: relative;
    &::before,
    &::after {
      content: "";
      position: absolute;
      height: 1px;
      width: 50vw;
      background-color: #ff002b;
      top: 50%;
      @media (max-width: 1920px) {
        height: 0.05vw;
      }
    }
    &::before {
      right: calc(100% + 160px);
      @media (max-width: 1920px) {
        right: calc(100% + 8.33vw);
      }
    }
    &::after {
      left: calc(100% + 160px);
      @media (max-width: 1920px) {
        left: calc(100% + 8.33vw);
      }
    }
  }
}

.button {
  border-radius: 50px;
  background: rgb(255, 0, 43);
  color: rgb(255, 255, 255);
  font-family: Cocogoose Narrow;
  font-size: 40px;
  font-weight: 900;
  line-height: 49px;
  letter-spacing: 0%;
  text-align: center;
  text-transform: uppercase;
  padding: 20px 60px;
  display: inline-block;
  cursor: pointer;
  @media (max-width: 1920px) {
    border-radius: 2.6vw;
    font-size: 2.08vw;
    line-height: 2.55vw;
    padding: 1.04vw 3.13vw;
  }
  @media (max-width: 800px) {
    border-radius: 50px;
    font-size: 40px;
    line-height: 49px;
    padding: 20px 60px;
  }
  &.green-button {
    background-color: #118400;
  }
}

.item {
  &__column {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
  }
  &__image {
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    margin-top: 50px;
    @media (max-width: 1920px) {
      margin-top: 2.6vw;
    }
    @media (max-width: 1920px) {
      margin-top: 7.6vw;
    }
  }
}

.swiper-arrow {
  cursor: pointer;
  position: absolute;
  z-index: 9;
  top: 42%;
  transform: translate(0, -50%);
  width: 157px;
  height: 157px;
  background: url("../../images/arrow-slider.svg") 0 0 / 100% 100% no-repeat;
  @media (max-width: 1920px) {
    width: 8.18vw;
    height: 8.18vw;
  }
  @media (max-width: 800px) {
    width: 16.75vw;
    height: 16.75vw;
  }
}
.swiper-arrow-prev {
  left: 0;
}
.swiper-arrow-next {
  right: 0;
  transform: translate(0, -50%) rotate(180deg);
}

.mob-el {
  display: none;
  @media (max-width: 800px) {
    display: block;
  }
}
.desk-el {
  display: block;
  @media (max-width: 800px) {
    display: none;
  }
}
